/* eslint-disable*/
import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { Navbar, Nav, NavDropdown, Button, Jumbotron } from 'react-bootstrap';
import Data from './data.js';
import Detail from './Detail.js'
import axios from 'axios';
import { NavLink,Link, Route, Switch } from 'react-router-dom';

function App() {

  let [shoes, shoes변경] = useState(Data);
  let [재고, 재고변경] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">KKaMi Shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>

        </Navbar.Collapse>
      </Navbar>

      <Switch>

        <Route exact path="/">

          <Jumbotron className="background">
            <h1>KKAMI DIET HURRY UP </h1>
            <p>
              까미는 너무 너무 깜찍하고 귀엽습니다.
              오늘 까미는 똥을 싸고 제게 오더니 제 팔에 똥을 묻히고 튀었답니다?!
              정말 정말 나쁜뇬입니다.
        </p>

            <p>
              <Button variant="primary">Learn more</Button>
            </p>
          </Jumbotron>

          <div className="container">
            <div className="row">
              {
                shoes.map((a, i) => { //a=어레이에 있던 하나하나의 데이터 i= 반복문으로 1씩 증가하는 정수
                  return <Card shoes={a} i={i} key={i} /> //shoes={a} 또는 shoes={shoes[i]}
                })
              }

            </div>
            <button className="btn btn-primary" onClick={() => {

              //로딩중이라는 ui 띄움 ~

              axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((result) => {
                  //로당즁 ui 삭제 ~
                  console.log(result.data);
                  shoes변경([...shoes, ...result.data]);  //...은 [] 을 벗겨 카피본을 만들어주세욘!
                
                }) //성공 시
                .catch(() => {
                  //로당즁 ui 삭제 ~
                  console.log('실패~') }) //실패 시
            }}>더보기</button>
          </div>
        </Route>


        <Route path="/detail/:id">
          <Detail shoes={shoes} 재고={재고} 재고변경={재고변경}  />
        </Route>

        {/* <Route path="/어쩌구" compontent={Modal} ></Route> */}

        <Route path="/:id">
          <div>아무거나 적었을 때 이거 보여주셈 ~~</div>
        </Route>
      </Switch>
      {/* 스위치 안에 담으면 라우터들이 하나씩 보임 */}

    </div>

  )
}

function Card(props) {

  return (
    <div className="col-md-4">
      <NavLink as={Link} to={"/detail/"+props.i}>
      <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} width="100%" />
      </NavLink>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}원</p>
      
    </div>
  )
}




export default App;
