import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
 padding : 20px;
`;

let 제목 = styled.h4`
  font-size : 25px;
  color : ${props => props.색상}
`;

// class Detail2 extends React.Component {
//   componentDidMount() {
//     //Detail2가 mount되었을 때 실행
//   }

//   componentWillUnmount() {
//     //Detail2 컴포넌트가 unmount 되기 직전에 실행할 코드
//   }


function Detail(props) {

  let [inputData, inputData변경] = useState('');
  let [alert, alert변경] = useState(true);
  useEffect(() => {

    // 2초 후 alert 창을 안보이게 해주세염, 2000 -> 2초
    let 타이머 = setTimeout(() => { alert변경(false) }, 2000);
    console.log('하잉')
    return () => { clearTimeout(타이머) }
  }, [alert]);

  let { id } = useParams();
  let history = useHistory();
  let 찾은상품 = props.shoes.find(x => x.id == id);

 

  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
        {/* props전송 시 ="red" 또는 = {'red'}로  */}
      </박스>

      {/* {inputData}
      <input onChange={(e) => { inputData변경(e.target.value) }} /> */}

      {
        alert === true
          ? (<div className="my-alert2">
            <p>재고가 얼마 남지 않아써오~.</p>
          </div>)
          : null
      }


      <div className="row">
        <div className="col-md-6">


          <img src={'https://codingapple1.github.io/shop/shoes' + (parseInt(id)+ 1) + '.jpg'} width="100%" />


        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>

          <Info 재고={props.재고}></Info>
          <button className="btn btn-danger" onClick={() => { props.재고변경([9, 10, 11]) }}>주문하기</button> &nbsp;
          <button className="btn btn-danger" onClick={() => {
            history.goBack(); //push('/'); 로 하면 /어쩌구로 경로를 이동시켜주세욤!!!!
          }}>뒤로가기</button>

        </div>
      </div>
    </div>
  )
}

function Info(props) {
  return (
    <p>재고 : {props.재고[0]}</p>
  )
}

export default Detail;